<template lang="pug">
    v-app(light)
        slot(name="app")
        v-content(style="background-color: #e9e9e9")
            v-container(fluid).adapter
                v-layout(align-center justify-center)
                    v-flex(column xs12 sm8 md4)
                        v-card.form-content
                            v-toolbar(dark)
                                v-btn(icon :disabled="disable" to="/")
                                    v-icon mdi-arrow-left
                                v-toolbar-title(style="margin-left: 0 !important;") {{ title }}
                                v-spacer
                                v-btn(icon :disabled="disable" @click.stop="reset")
                                    v-icon mdi-refresh
                            v-card-text.v-input-group-mobile-padding
                                slot(name="form")
                            v-card-actions
                                v-menu(v-if="more" top origin="bottom left" transition="scale-transition")
                                    template(#activator="{ on }")
                                        v-btn(flat color="blue darken-1" :disabled="disable" v-on="on").mx-2.mb-2 {{ more.text }}
                                    v-list
                                        v-list-tile(v-for="(item, index) in more.items" :key="index" :to="item.to")
                                            v-list-tile-title {{ item.title }}
                                v-spacer
                                slot(name="action")
                            Footer(fixed).hidden-md-and-up
</template>

<script>
    import Footer from '@/components/common/Footer.vue'
    export default {
        name: 'Layout',
        components: {
            Footer
        },
        props: {
            title: {
                type: String,
                default: ''
            },
            disable: {
                type: Boolean,
                default: false
            },
            more: {
                type: Object,
                default: null
            },
        },
        data () {
            return {
            }
        },
        methods: {
            reset () {
                if (!this.$parent) return;
                if (!this.$parent.$refs) return;
                let form = this.$parent.$refs.form;
                if (typeof form === 'object' && typeof form.reset === 'function')
                    form.reset();
            }
        }
    }
</script>

<style scoped lang="stylus">
    .adapter
        display flex
        height 100%
        @media (max-width 768px)
            padding 0 !important
            align-items normal
            display inherit
            height inherit
    .form-content
        @media (max-width 768px)
            height 100vh
</style>
