<template lang="pug">
    AuthLayout(:title="title" :more="more" :disable="form.disable")
        template(#form)
            v-form(ref="form" lazy-validation)
                v-text-field(
                    :label="email"
                    append-icon="mdi-close"
                    prepend-icon="mdi-email"
                    v-model="form.email"
                    type="email"
                    :rules="emailRules"
                    :disabled="form.disable"
                    @click:append="form.email = null"
                    required
                )
                v-text-field(
                    :label="password"
                    prepend-icon="mdi-lock"
                    :append-icon="form.showPassword ? 'mdi-eye-off' : 'mdi-eye'"
                    v-model="form.password"
                    :type="form.showPassword ? 'text' : 'password'"
                    :rules="passwordRules"
                    :disabled="form.disable"
                    @click:append="form.showPassword = !form.showPassword"
                    required
                )
                v-text-field(
                    :label="confirmPassword"
                    prepend-icon="mdi-lock"
                    :append-icon="form.showPassword ? 'mdi-eye-off' : 'mdi-eye'"
                    v-model="form.confirmPassword"
                    :type="form.showPassword ? 'text' : 'password'"
                    :rules="confirmPasswordRules"
                    :disabled="form.disable"
                    @click:append="form.showPassword = !form.showPassword"
                    required
                )
                v-text-field(
                    :label="nickname"
                    :rules="nicknameRules"
                    v-model="form.nickname"
                    @click:append="form.nickname = null"
                    :disabled="form.disable"
                    type="text"
                    prepend-icon="mdi-flag"
                    append-icon="mdi-close"
                    color="blue darken-1"
                    required
                )
        template(#action)
            v-btn(
                block
                color="blue darken-1"
                :disabled="form.disable"
                @click="preSubmit"
            ).mx-2.mb-2.white--text {{ register }}
        template(#app)
            Snackbar(ref="snackbar")
            v-dialog(persistent v-model="codeDialog" max-width="400px")
                v-card
                    v-card-title.subheading {{ codeTitle }}
                    v-card-text.pt-0
                        v-form(ref="formCode")
                            v-text-field(
                                :label="code"
                                :rules="codeRules"
                                v-model="form.code"
                                type="text"
                                color="blue darken-1"
                                prepend-icon="mdi-code-tags"
                                clearable
                                required
                            )
                    v-card-actions
                        v-btn(
                            flat
                            color="pink darken-1"
                            @click="cancelSubmit"
                        ).white--text {{ cancelCode }}
                        v-spacer
                        v-btn(
                            color="blue darken-1"
                            @click="submit"
                            :loading="form.loading"
                        ).white--text {{ confirmCode }}
</template>

<script>
    import AuthLayout from '@/components/auth/AuthLayout.vue'
    import Snackbar from '@/components/common/Snackbar.vue'
    export default {
        name: 'AuthRegister',
        components: {
            AuthLayout, Snackbar
        },
        data() {
            return {
                title: '',
                email: '* ',
                emailRules: [
                    v => !!v || '!',
                    v => /^([A-Za-z0-9_\-]{3,})@(([A-Za-z0-9_\-]+)\.([A-Za-z]{2,})(\.([A-Za-z]{2,}))?)$/.test(v) || '!'
                ],
                password: '* ',
                passwordRules: [
                    v => !!v || '!',
                    v => v && v.length >= 6 || ' 6!',
                ],
                confirmPassword: '* ',
                confirmPasswordRules: [
                    v => !!v || '!',
                    v => v === this.form.password || '!'
                ],
                nickname: '* ',
                nicknameRules: [
                    v => !!v || '!',
                    v => v && /^([a-zA-Z0-9_]{3,16})$/.test(v) || ' 3~16 !'
                ],
                codeDialog: false,
                codeTitle: ': ',
                code: '* ',
                codeRules: [
                    v => !!v || '!',
                ],
                register: '',
                cancelCode: '?',
                confirmCode: '',
                more: {
                    text: '',
                    items: [
                        { title: '?', to: '/login' },
                        { title: '?', to: '/forget' }
                    ]
                },
                form: {
                    email: null,
                    password: null,
                    confirmPassword: null,
                    nickname: null,
                    code: null,
                    showPassword: false,
                    disable: false,
                    loading: false,
                }
            }
        },
        methods: {
            preSubmit (evt) {
                evt.preventDefault();
                if (this.$refs.form.validate()) {
                    this.form.disable = true;
                    this.codeDialog = true;
                }
            },
            cancelSubmit (evt) {
                evt.preventDefault();
                this.codeDialog = false;
                this.form.disable = false;
            },
            async submit (evt) {
                evt.preventDefault();
                if (!this.$refs.formCode.validate())
                    return;
                this.form.loading = true;
                this.$store.dispatch('register', {
                    email: this.form.email,
                    password: this.form.password,
                    nickname: this.form.nickname,
                    code: this.form.code,
                })
                    .then(() => {
                        this.$refs.snackbar.show('success', '!', () => {
                            this.form.disable = false;
                            this.$router.push('/login');
                        });
                    })
                    .catch(err => {
                        let msg = err.message;
                        if (err.response && err.response.data.msg)
                            msg = err.response.data.msg;
                        this.$refs.snackbar.show('error', ': ' + msg);
                        this.form.loading = false;
                    })
            }
        }
    }
</script>

<style scoped lang="stylus">
</style>
